.rs-select{display:block;width:auto;height:auto;position:relative;font-family:sans-serif}.rs-select.is-disabled{color:#dbdbdb;background:#eee}.rs-select-button{cursor:pointer;border:1px solid #ddd;background:#f2f2f2;line-height:40px;font-size:20px;white-space:nowrap}.rs-select-indicator{position:absolute;right:0;top:0;width:40px;height:40px;background:grey;text-align:center}.rs-select-indicator::after{content:'❯';font-size:14px}.rs-select-options{display:block;position:absolute;width:auto;min-width:100%;height:auto;top:100%;left:0;z-index:10;margin:0;padding:0;list-style:none}.direction-up .rs-select-options{top:auto;bottom:100%}.rs-select-option{cursor:pointer;line-height:26px;border:1px solid #ddd;border-top:none}.rs-select-option.is-selected{background:#2199e8;color:#ffffff}.rs-select-option.is-disabled{cursor:default;background:#eee;color:#dbdbdb}
